    outline: none;
    border-radius: 20px;
}

.ProgressBar_progress__3DnV-{
    height: 4px;
    background: var(--fontPrimary);
    border-radius: 20px;
    position: relative;
    transition: width 0.3s ease;
}

.ProgressBar_progress__3DnV-::after{
    content: "";
    height: 12px;
    width: 12px;
    position: absolute;
    right: 0;
    border-radius: 100%;
    background-color: var(--fontPrimary);
    transform: translateY(-50%);
    top: 50%;
    cursor: pointer;
}

.ProgressBar_tooltip__hgPPP{
    position: absolute;
    top: -70px;
    /* left: calc(70% - 65px); */
    width: 130px;
    background-color: var(--fontPrimary);
    color: #fff;
    padding: 0.8rem 1rem;
    border-radius: 7px;
    font-size: 12px;
    font-weight: 600;
    display: none;
}

.ProgressBar_tooltip__hgPPP::after{
    content: "";
    width: 15px;
    height: 15px;
    background: var(--fontPrimary);
    border-radius: 4px;
    bottom: 0;
    left: 50%;
    position: absolute;
    transform: rotate(45deg) translate(-50%,100%);
}
